import { Table, Typography, Image } from 'antd'
import {carts} from '../data'

const columns = [
    {
        title: 'Name',
        dataIndex: 'name',
        key: 'name',
        render: (text) => <Typography.Text copyable>{text}</Typography.Text>
    },
    {
        title: 'Number',
        dataIndex: 'number',
        key: 'number'
    },
    {
        title: 'Class',
        dataIndex: 'classification',
        key: 'classification',
        filters: [
            {
                text: 'Seed',
                value: 'Seed'
            },
            {
                text: 'Run',
                value: 'Run'
            },
            {
                text: 'Sleep',
                value: 'Sleep'
            },
            {
                text: 'Drink',
                value: 'Drink'
            },
            {
                text: 'Eat',
                value: 'Eat'
            },
        ],
        onFilter: (value, item) => item.classification.includes(value)
    },
    {
        title: 'Rate',
        dataIndex: 'fleeRate',
        key: 'fleeRate',
        sorter: (a, b) => a.fleeRate - b.fleeRate
    },
    {
        title: 'Image',
        dataIndex: 'image',
        key: 'image',
        render: (image) => <Image src={image} alt="carts" width={150} />
    }
]

const dataSource = carts.map(item => ({...item, key: item.id}))

const _Table = ({rows = 4}) => {
    return (
        <Table dataSource={dataSource}
               columns={columns}
               pagination={{
                pageSize: rows
                // defaultPageSize: '2',
                // showSizeChanger: true,
                // pageSizeOptions: ['2', '4', '6', '10']
            }}
        />
    )
}

export default _Table